import React, { PureComponent } from 'react';
import { withTheme } from '@material-ui/core';
import { AreaSeries, Chart, SplineSeries, Tooltip } from '@devexpress/dx-react-chart-material-ui';
import { EventTracker } from '@devexpress/dx-react-chart';
import faker from 'faker';

class TrendingChart extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: this.generateData(2.5, 12, 0.5),
      targetItem: null
    };
  }

  generateData(start, end, step) {
    const data = [];
    for (let i = start; i < end; i += step) {
      data.push({
        splineValue: faker.datatype.number({ min: 100, max: 900 }),
        lineValue: faker.datatype.number({ min: 100, max: 900 }),
        argument: i
      });
    }
    return data;
  }

  render() {
    return (
      <Chart
        data={this.state.data}
        height={this.props.theme.spacing(10)}
      >
        <AreaSeries
          valueField="splineValue"
          argumentField="argument"
        />
        <SplineSeries
          valueField="splineValue"
          argumentField="argument"
        />
        <EventTracker />
        <Tooltip
          targetItem={this.state.targetItem}
          onTargetItemChange={targetItem => this.setState({ targetItem })}
        />
      </Chart>
    );
  }
}

export default withTheme(TrendingChart);
