import React, { PureComponent } from 'react';
import { withTheme } from '@material-ui/core';
import { AreaSeries, Chart, SplineSeries } from '@devexpress/dx-react-chart-material-ui';
import faker from 'faker';

class TrendingChart extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: this.generateData(2.5, 12, 0.5)
    };
  }

  generateData(start, end, step) {
    const data = [];
    for (let i = start; i < end; i += step) {
      data.push({
        splineValue: faker.datatype.number({ min: 100, max: 900 }),
        lineValue: faker.datatype.number({ min: 100, max: 900 }),
        argument: i
      });
    }
    return data;
  }

  render() {
    return (
      <Chart
        data={this.state.data}
        height={this.props.theme.spacing(7.5)}
      >
        <AreaSeries
          valueField="splineValue"
          argumentField="argument"
        />
        <SplineSeries
          valueField="splineValue"
          argumentField="argument"
        />
      </Chart>
    );
  }
}

export default withTheme(TrendingChart);
